import React from "react";
import { Button, Form, Modal, Input, InputNumber, Checkbox } from "antd";

import OwnFetch from "../../api/OwnFetch"; //

const FormItem = Form.Item;

@Form.create()
export default class CccePayIndex extends React.Component {
    state = {
        editData: {},
        loading: false
    };

    componentDidMount() {
        this.getData();
    }

    //
    getData = () => {
        OwnFetch("cccePay_list", {}).then(res => {
            if (res && res.code == 200) {
                this.setState({ editData: res.data });
            }
        });
    };

    handleSubmit = () => {
        this.props.form.validateFields((err, values) => {
            if (err) {
                return;
            }
            Modal.confirm({
                title: "",
                content: "!",
                okText: "",
                cancelText: "",
                onOk: () => {
                    this.setState({ loading: true });
                    OwnFetch("cccePay_update", values).then(res => {
                        if (res && res.code == 200) {
                            Modal.success({ title: "" });
                        }
                        this.setState({ loading: false });
                    }).catch(error => this.setState({ loading: false }));
                }
            });
        });
    };

    render() {
        const { getFieldDecorator } = this.props.form;
        let editData = this.state.editData;
        let formItemLayout = {
            labelCol: {
                xs: { span: 24 },
                sm: { span: 6 }
            },
            wrapperCol: {
                xs: { span: 24 },
                sm: { span: 14 }
            }
        };
        return (
            <div className="new_div_context">
                <Form onSubmit={this.handleSubmit} style={{ padding: 100 }}>

                    <FormItem label="url" {...formItemLayout} hasFeedback>
                        {getFieldDecorator("url", {
                            initialValue: editData.url,
                            rules: [
                                {
                                    required: true,
                                    message: "url!"
                                }
                            ]
                        })(<Input placeholder="url" />)}
                    </FormItem>


                    <FormItem label="" {...formItemLayout} hasFeedback>
                        {getFieldDecorator("wkey", {
                            initialValue: editData.wkey,
                            rules: [
                                {
                                    required: true,
                                    message: "key!"
                                }
                            ]
                        })(<Input placeholder="key" />)}
                    </FormItem>

                    <FormItem label="" {...formItemLayout} hasFeedback>
                        {getFieldDecorator("wmerno", {
                            initialValue: editData.wmerno,
                            rules: [
                                {
                                    required: true,
                                    message: "!"
                                }
                            ]
                        })(<Input placeholder="" />)}
                    </FormItem>

                    <FormItem label="" {...formItemLayout} hasFeedback>
                        {getFieldDecorator("zkey", {
                            initialValue: editData.zkey,
                            rules: [
                                {
                                    required: true,
                                    message: "key!"
                                }
                            ]
                        })(<Input placeholder="key" />)}
                    </FormItem>

                    <FormItem label="" {...formItemLayout} hasFeedback>
                        {getFieldDecorator("zmerno", {
                            initialValue: editData.zmerno,
                            rules: [
                                {
                                    required: true,
                                    message: "!"
                                }
                            ]
                        })(<Input placeholder="" />)}
                    </FormItem>

                    <FormItem style={{ textAlign: 'center' }}>
                        <Button
                            type="primary"
                            htmlType="submit"
                            disabled={this.state.loading}
                        >
                            
            </Button>
                    </FormItem>
                </Form>
            </div>
        );
    }
}



